export default {
    props:{
        listsData:{
            type: Array,
            default:[],
        },
    },
  watch:{
    listsData(){
      console.log(this.listsData)
    }
  },
    data() {
        return {};
    },
    methods: {
      seeMore(item){
        console.log(item)
        if(item.toType==='shop'){
          if(item.selectShopType==='brand'){
            this.toQueryLink('/pages/brand/lists',{
              shopid:item.toId
            })
          }else{
            this.toQueryLink('/pages/shop/index',{
              shopid:item.toId
            })
          }
        }else{
          this.toQueryLink('/pages/goods/lists',{
            gc_id:item.toId
          })
        }
      }
    },
    mounted() {
    },
};
